/**
* @name: Movies & Series App
* @description: Aqui encontrará los estilos globales de la aplicación
*/

/* Variables */
:root {
  --dark-blue: #4365b3;
  --blue: #5eb5d7;
  --white-blue: #f3fffc;
  --light-blue: #bbfaf0;
  --light-dark-blue: #83eded;
  --black: #121212;
  --max-width: 1920px;
}

/* Tipography*/

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Stylish", sans-serif;
  margin: 0;
  line-height: 1.5;
}
body {
  font-family: "Open Sans", sans-serif;
  line-height: 1.4;
  background: #f3fffc;
  background: var(--white-blue);
}

/* Components Styles */
.btn {
  min-width: 64px;
  padding: 8px;
  border: none;
  background: #acacac;
  font-weight: 400;
}

.btn-primary {
  background: #4365b3;
  background: var(--dark-blue);
  color: #f3fffc;
  color: var(--white-blue);
  cursor: pointer;
}

.btn-secondary {
  background: #83eded;
  background: var(--light-dark-blue);
  border: 1px solid #4365b3;
  border: 1px solid var(--dark-blue);
  cursor: pointer;
  margin: 0 auto;
}

/**
* @name: Header Component Styles
*/

.Header {
  background: var(--dark-blue);
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 1rem;
}

.Header-link {
  color: var(--white-blue);
}

.Header-link:hover {
  color: var(--light-blue);
}

/**
* @name: SearchList Component Styles
*/

.SearchList {
  padding: 1rem 0;
}

.SearchListGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}

.SearchList-item {
  background: var(--dark-blue);
}

.SearchList-item-info {
  background: var(--dark-blue);
  color: var(--white-blue);
  padding: 10px;
  margin: 0;
  text-align: center;
}

.responsive {
  width: 100%;
  height: auto;
}

.ShowDetails {
  padding: 0.5rem 0;
  font-size: 14px;
  text-align: center;
  color: var(--white-blue);
}

/* Media Queries */
@media screen and (max-width: 968px) {
  .SearchListGrid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 668px) {
  .SearchListGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 450px) {
  .SearchListGrid {
    grid-template-columns: 1fr;
  }
}

/**
* @name: Modal Component Styles
*/
.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 99;
}

.ModalItem {
  position: relative;
  top: 5rem;
  background-color: var(--white-blue);
  color: var(--dark-blue);
  padding: 1rem;
  max-width: 400px;
  min-width: 250px;
  box-shadow: 0px 0px 5px 0px rgba(67, 101, 179, 0.41);
}

.ModalItem-close {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: var(--white-blue);
  padding: 0.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: bold;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
}
.ModalItem-close:hover {
  color: darkred;
}

.ModalContent {
  display: flex;
  flex-direction: column;
}

.ModalContent-title {
  text-align: center;
  padding: 0.5rem 0;
}
.ModalContent-subtitle {
  padding: 0.5rem;
}

.ModalContent-img {
  width: 180px;
  height: auto;
}

.ModalContent-body {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

/**
* @name Search Page Styles
*/

.MovieHome {
  padding: 2rem;
  text-align: center;
}

.MovieHome-title {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.MovieHomeForm-search {
  max-width: 500px;
  min-width: 300px;
  padding: 8px;
}

.MovieHomeForm-checkboxes {
  max-width: 500px;
  min-width: 300px;
  display: flex;
  justify-content: center;
  margin: 1rem auto;
}

.MovieHomeForm-checkboxes label {
  padding: 0 0.5rem;
}

.MovieHome-results {
  width: 100%;
  padding: 1rem 0;
  text-align: left;
}

.LoadMore {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

/**
* @name: Not Found Component Styles
*/

.NotFound-title {
  padding: 1rem;
}

.NotFound-link {
  color: var(--dark-blue);
  text-align: center;
  padding: 1rem;
}

